  --pfe-broadcasted--color--text: var(--pfe-theme--color--surface--darkest--text, #fff);
  --pfe-broadcasted--color--ui-link: var(--pfe-theme--color--surface--darkest--ui-link, #99ccff);
  --pfe-broadcasted--color--ui-link--hover: var(--pfe-theme--color--surface--darkest--ui-link--hover, #cce6ff);
