}

.on-light {
  --pfe-broadcasted--color--text: var(--pfe-theme--color--surface--lightest--text, #333);
  --pfe-broadcasted--color--ui-link: var(--pfe-theme--color--surface--lightest--ui-link, #06c);
  --pfe-broadcasted--color--ui-link--hover: var(--pfe-theme--color--surface--lightest--ui-link--hover, #003366);
  --pfe-broadcasted--color--ui-link--visited: var(--pfe-theme--color--surface--lightest--ui-link--visited, rebeccapurple);
